import { Upload } from 'gw-vue-ui';
export default {
    components: {
        GwUpload: Upload
    },
    data() {
        const imgListArr = [];
        const defaultImgListArr = [];
        const hasImgListArr = [{ src: 'https://fubei-official-resources.oss-cn-beijing.aliyuncs.com/image-resources/introduce/pc/whats_fubei_bg2_20191016.png' }];
        return {
            maxSize: 5,
            imgList: imgListArr,
            defaultImgList: defaultImgListArr,
            hasImgList: hasImgListArr
        };
    },
    methods: {
        /**
         * @function 与原生交互
         */
        brigeMethod() {
            console.log('与原生交互');
        },
        /**
         * @function 文件上传成功
         */
        successUpload(files) {
            this.defaultImgList.push(files);
        },
        /**
         * @function 删除与原生交互元素
         */
        deleteAction(index) {
            this.imgList.splice(index, 1);
        },
        /**
         * @function 删除默认上传元素
         */
        deleteDefaultAction(index) {
            this.defaultImgList.splice(index, 1);
        },
        /**
         * @function 上传前校验
         */
        beforeUpload(file) {
            const isJPG = file.type === 'image/jpeg';
            const isLt2M = file.size / 1024 / 1024 < 2;
            if (!isJPG) {
                alert('上传头像图片只能是 JPG 格式!');
            }
            if (!isLt2M) {
                alert('上传头像图片大小不能超过 2MB!');
            }
            return isJPG && isLt2M;
        }
    }
};
